:root {
    --dark-bg: #252627;
    --checked-clr: #7494ea;
  }

.navbar{
    padding: 1em 2em;
    display: flex;
    justify-content: space-between;
    align-items:center;
    background-color: #101010;
    border-bottom: .5px solid $color3;
    @include responsive(m){
        display: none;
    }

    &_toggle{
      display: flex;
      &_input {
        cursor: pointer;
        -webkit-appearance: none;
        position: relative;
        width: 57px;
        height: 27px;
        border: 2px solid #ccdbdc;
        outline: none;
        border-radius: 30px;
        transition: 300ms;
        &::before{
          content: "";
          position: absolute;
          top: 1.5px;
          left: 2px;
          width: 20px;
          height: 20px;
          background: #d1c9c9;
          border-radius: 50%;
          transition: background 300ms ease, transform 300ms ease;
        }
        &:checked{
          border-color: var(--checked-clr);
          &::before{
            transform: translateX(30px);
            background: var(--checked-clr);
          }
        }
      }
    }

    &_menu{
        color:$color3;
        display: flex;
        list-style: none;
        justify-content: space-around;
        width:400px;
        align-items:center;
        font-weight: 200;
        font-size: 18px;
        &_list{
          text-decoration: none;
          transition: opacity .3s ease .3s;
          color: $color3;
      }
    }
}

.container{
  display: none;
  
  @include responsive(m){
    display: flex;
    background-color: rgb(240, 240, 240);
  position: fixed;
  bottom: 0;
  z-index: 100;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 60px;
}
}

.tabbar{
  display: flex;
  align-items: center;
  justify-content: center;
}

ul{
  display: flex;
  justify-content: center;
  align-items: center;
}

// Mobile View

.mnavbar{
  display: none;
  @include responsive(m){
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 99;
}
  &_menu{
    border: 2px solid blue;
    display: flex;
    background: transparent;
    padding: 1.5em .5em;
    position: fixed;
    bottom: 10px;
    width: 90%;
    border-radius: 20px;
    justify-content: space-around;
    align-items: center;
    margin: 1em 0;
    z-index: 99;
    &_list{
      display: flex;
      align-items: center;
    }
  }
}

.active{
  background-color: red;
  color: #fff;
}

.icon path{
  fill: transparent;
  stroke-width: 10;
  stroke: rgb(255, 255, 255);
}

.icon.document path{
  stroke-dasharray: 1750;
  stroke-dashoffset: 0;
}

.icon.document:hover path{
  animation: animate-document 2s linear forwards;
}

@keyframes animate-document{
  0%{
    stroke-dashoffset: 0;
  }
  40%{
    stroke-dashoffset: 1750;
  }
  80%{
    stroke-dashoffset: 3500;
    fill: transparent;
  }
  100%{
    stroke-dashoffset: 3500;
    fill: yellowgreen;
  }
}

